.home {
  .home_top {

    .left {
      height: 100%;
    }

    .right {
      border: 1px solid rgb(116, 109, 109);
      box-shadow: 0 0 15px rgb(180, 174, 174);
      padding: 17px;
      .button_area {
        margin-top: 20px;
      }
    }
  }

  .home_bottom {
    margin-top: 50px;

    a {
      display: block;


      .image_card {
        transition: all 0.3s ease-in-out;
        &:hover{
          transition: all 0.3s ease-in-out;
          box-shadow: 0 0 10px rgb(82, 77, 77);
          transform: scale(1.01);
        }
        img {
          height    : 350px;
          object-fit: fill;
          width     : 100%;
          border: 1px solid black;
        }

      }
    }

    .button_area {
      display        : flex;
      justify-content: center;
      padding        : 60px 0;
    }
  }
}